import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LibroclickedService {

  libros: any;
  librosVistos:Array<object>

  constructor() 
  {
    this.libros = []
    this.librosVistos = []

  }

  //no tiene el guion bajo porq no proviene de una vista
  libroVisto(librovisto)
  {
    this.librosVistos.push(librovisto)
  }

  verLibrosVistos()
  {
    return this.librosVistos;  
  }
   
  verListado()
  {
    return this.libros;
  }
}
