import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';

import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { Prueba01Component } from './prueba01/prueba01.component';
import { RandomComponent } from './random/random.component';
import { FirmaComponent } from './firma/firma.component';
import { LibrosComponent } from './libros/libros.component';
import { SobreNostrosComponent } from './sobre-nostros/sobre-nostros.component';
import { CabeceraComponent } from './cabecera/cabecera.component';
import { InicioComponent } from './inicio/inicio.component';

import { LibroclickedService } from './libroclicked.service'
import { HttpClientModule } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';


import {MatProgressSpinnerModule} from '@angular/material/progress-spinner';
import { RendererComponent } from './renderer/renderer.component';
import { FooterComponent } from './footer/footer.component';
import { InformacionComponent } from './informacion/informacion.component'; 
//el orden de los factores 
const rutas: Routes = 
[
  {path:'', component:InicioComponent, pathMatch:'full'},
  {path:'listado-libros', component:LibrosComponent},

  //ruta por defecto al final -> 
  
  {path:'informacion/:libroId', component: InformacionComponent},
  {path:'informacion', redirectTo:'/'},

  {path:'**', redirectTo: '/'},

]


@NgModule({
  declarations: [
    AppComponent,
    Prueba01Component,
    RandomComponent,
    FirmaComponent,
    LibrosComponent,
    SobreNostrosComponent,
    CabeceraComponent,
    InicioComponent,
    RendererComponent,
    FooterComponent,
    InformacionComponent
  ],
  imports: [
    BrowserModule, FormsModule, RouterModule.forRoot(rutas), HttpClientModule, BrowserAnimationsModule
    ,MatProgressSpinnerModule
  ],
  providers: 
  [
    LibroclickedService


  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
