import { Component, OnInit, Renderer2 } from '@angular/core';
import { LibroclickedService } from '../libroclicked.service';
import { HttpClient, HttpResponse } from '@angular/common/http'

@Component({
  selector: 'app-libros',
  templateUrl: './libros.component.html',
  styleUrls: ['./libros.component.css']
})
export class LibrosComponent implements OnInit {

  //wtf is any?
  libros:any
  errorHttp: boolean;
  asyncOperationLoaded: boolean;

  constructor(private htpp: HttpClient, public LibroClicked:LibroclickedService, private ren:Renderer2) 
  {
    
    /*
    this.libros = [
      {id:'1', titulo: 'Te veré bajo el hielo', autor:'Robert Bryndza'},
      {id:'2', titulo: 'Dime quién soy', autor:'Julia Navarro'},
      {id:'3', titulo: 'El día que se perdió la cordura', autor:'Javier Castillo'}
    ]
    */
    
    

  }


  ngOnInit(): void 
  {
    this.cargarLista();
    
  }

  cargarLista() {

   
    
    this.asyncOperationLoaded = true;
    //  http://localhost:3000/libros
    //'assets/lista-libros.json'
    this.htpp.get('assets/lista-libros.json')
    
    .subscribe
    (
        (respuesta: Response) => 
        {
          this.LibroClicked.libros = respuesta;
          //this.libros = respuesta;
          this.asyncOperationLoaded = false;
        },
        (respuesta: Response) => 
        {
          this.errorHttp = true; this.asyncOperationLoaded = true;
        }

        
    )
  }

  agregarLibro(_libroVisto)  
  {
    //alert(_libroVisto.autor);
    this.LibroClicked.libroVisto(_libroVisto);
  }
  clearElement:HTMLElement
  activeMethod(element:HTMLElement)
  {
    if(this.clearElement)
      {
        let nuevoelemento = this.ren.createElement("span");
        
      }


    this.clearElement = element;
  }


  showInfo(_libro): void
  {
    alert(_libro.autor);
  }

}
