import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params} from '@angular/router'
import { LibroclickedService } from '../libroclicked.service';

@Component({
  selector: 'app-informacion',
  templateUrl: './informacion.component.html',
  styleUrls: ['./informacion.component.css']
})
export class InformacionComponent implements OnInit {
  libroId: number;
  libroClicked: any;

  constructor(private rutaUsuario: ActivatedRoute, public libros:LibroclickedService) { }

  ngOnInit(): void {

    
    this.rutaUsuario.params.subscribe(p => 
      { 
        this.libroId = p['libroId'];
        this.libroClicked = this.buscarLibro();
      })
  }
  buscarLibro()  {
    return this.libros.verListado().filter(this.filtroId, this.libroId)[0];
  }
  filtroId(libro) {
    return libro.id = this;
  }

}
